* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }
  #nav {
    height: 55px;
    width: 100%;
    background-color: rgb(74, 7, 7);
  
    display: flex;
    justify-content: center;
  }
  #nav > article {
    height: 55px;
    width: 95%;
    display: flex;
  }
  #nav > article > .logo {
    height: 55px;
    width: 20%;
  
    display: flex;
  }
  #nav > article > .logo > ul {
    display: flex;
    list-style-type: none;
    font-family: Arial, Helvetica, sans-serif;
    justify-content: space-around;
    align-items: center;
  
    height: 55px;
  }
  #nav > article > .logo > ul > li {
    text-decoration: none;
  
    font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
      "Lucida Sans", Arial, sans-serif;
    margin: 7px;
  
    font-size: 20px;
  
    color: white;
  }
  #nav > article > .menu {
    height: 55px;
    width: 60%;
  }
  #nav > article > .menu > ul {
    display: flex;
    list-style-type: none;
    font-family: Arial, Helvetica, sans-serif;
    justify-content: space-around;
    align-items: center;
  
    height: 55px;
  }
  #nav > article > .menu > ul > li > a {
    text-decoration: none;
    color: white;
    font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
      "Lucida Sans", Arial, sans-serif;
  
    font-weight: 500;
    font-size: 15px;
  }
  #nav > article > .sign {
    width: 20%;
    height: 55px;
  }
  #nav > article > .sign > ul {
    display: flex;
    list-style-type: none;
    font-family: Arial, Helvetica, sans-serif;
    justify-content: right;
    align-items: center;
  
    height: 55px;
  }
  #nav > article > .sign > ul > li > a {
    margin: 8px;
    color: white;
    text-decoration: none;
  }
  