#search {
    width: 100%;
    margin-left: 10px;
    margin-bottom: 20px;
  }
  #search > p {
    margin-top: 4px;
    margin-left: 4px;
    font-size: 15px;
  }
  #search > ul {
    display: flex;
    list-style-type: none;
    align-items: center;
  }
  #search > ul > li {
    margin: 5px;
  }
  #search > ul > li > input {
    margin-left: -10px;
    width: 300px;
    padding: 7px;
    border: 1px solid black;
  
    border-left-style: hidden;
  }
  #search > ul > li > button {
    padding: 7px 10px;
    margin-left: -10px;
    border: 1px solid black;
    border-left-style: hidden;
    background-color:  #62B6B7;
    color: white;
  }
  #search > ul > li > i {
    padding: 6.5px;
    border: 1px solid black;
    border-right: hidden;
  }
  #search > .side {
    height: 10px;
  }
  #search > .side > ul {
    display: flex;
    list-style-type: none;
    justify-content: left;
    align-items: center;
  }
  #search > .side > ul > li {
    margin-left: 4px;
  }
  