#main{
    width: 100%;
    margin-top: 10px;
    margin-left: 10px;
    display: flex;
  
}
#main>.sidebar{
    width: 15%;
}
#main>.sidebar>table{
    width: 100%;

}
#main>.sidebar>table>tr>a{
  
    text-decoration: none;
    margin: 10px 0 ;
    font-size: 13px;
    color: black;
}
#main>.mainbox{
    width: 83%;
   
}

#main>.mainbox>.mainbar{
    width: 100%;

    display: flex;
   
    text-align: center;
    
}

#main>.mainbox>.mainbar>div{
    
    border: 1px solid rgb(186, 181, 181);
    border-radius: 2px;
    align-items: center;
    height: 400px;
    margin:4px;
    
}

#main>.mainbox>.mainbar .box .div1>ul{
    list-style-type: none;
    align-items: center;
    
    
}

#main>.mainbox>.mainbar .box .div1 >ul>li{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 14px;
    margin: 7px;
}
#main>.mainbox>.mainbar .box .div1>ul>li>p{
    color: grey;
    font-size: 12px;
}
#main>.mainbox>.mainbar .box .div2>a>button{
    color: #62B6B7;
    
    border-style: hidden;
    font-size: 18px;
    background-color: white;
    margin-top: 10px;
}
#main>.mainbox>.mainbar .box .line{
    background-color: rgb(74, 7, 7);
    height: 10px;
    margin-top: 7px;
    border-radius: 2px;
}

#main>.mainbox> .mainbar2{
    width: 100%;

    display: flex;
    
    text-align: center;
   
}

#main>.mainbox> .mainbar2>div{
    width:85%;
 
    border: 1px solid rgb(186, 181, 181);
    border-radius: 2px;
    align-items: center;
    height: 400px;
    margin:9px;
}

#main>.mainbox> .mainbar2> .box .div1>ul{
    list-style-type: none;
    align-items: center;
    
    
}

#main>.mainbox> .mainbar2> .box .div1 >ul>li{
    margin: 7px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 14px;
}
#main>.mainbox> .mainbar2 >.box .div1>ul>li>p{
    color: grey;
    font-size: 12px;
}
#main>.mainbox> .mainbar2 >.box .div2>a>button{
    color: #62B6B7;
   
    border-style: hidden;
    font-size: 18px;

    background-color: white;
    margin-top: 10px;
}
#main>.mainbox >.mainbar2 >.box .line{
    height: 10px;
    margin-top: 14px;
    border-radius: 2px;
    background-color: rgb(74, 7, 7);
}
